import React, { Component } from 'react'
import './App.css';



class summary extends Component {
 
   

    render() {
        
        
        const summary = this.props.summary; 
        const currentDate = this.props.currentDate; 

        return (
				
		  
		  <div className="containe-fluid row123">
		  
            <div className="row">
			
			
			
            <div className="col s4 m4 l2">
			    <div className="NewConfirmed">
                    <h5>New Confirmed </h5>
                    <h5>{summary.NewConfirmed}</h5>
                    <h4>{new Date(currentDate).toDateString()}</h4>
                 </div>
		     </div>    
            


				<div className="col s4 m4 l2">
                 <div className="TotalConfirmed">
                    <h5>Total Confirmed </h5>
                    <h5>{summary.TotalConfirmed}</h5>
                     <h4>{new Date(currentDate).toDateString()}</h4>
                 </div>
				 </div>

				<div className="col s4 m4 l2">
                 <div className="NewDeaths">
                    <h5>New Deaths </h5>
                    <h5>{summary.NewDeaths}</h5>
                     <h4>{new Date(currentDate).toDateString()}</h4>
                 </div>
                 </div>

				<div className="col s4 m4 l2">
                 <div className="TotalDeaths">
                    <h5>Toal Deaths </h5>
                    <h5>{summary.TotalDeaths}</h5>
                     <h4>{new Date(currentDate).toDateString()}</h4>
                 </div>
                 </div>

				<div className="col s4 m4 l2">
                 <div className="NewRecovered">
                    <h5>New Recovered </h5>
                    <h5>{summary.NewRecovered}</h5>
                     <h4>{new Date(currentDate).toDateString()}</h4>
                 </div>
                 </div>

				<div className="col s4 m4 l2">
                 <div className="TotalRecovered">
                    <h5>Total Recovered </h5>
                    <h5>{summary.NewConfirmed}</h5>
                     <h4>{new Date(currentDate).toDateString()}</h4>
                 </div>
                 </div>

                
              </div>
			
			</div>
			
        )
    }
}

export default summary;











